import React from 'react';
import './App.css';
import Particles from "react-tsparticles";
import {Config} from "./config/Config";

class Name extends React.Component<{ name: string }> {
    render() {
        return <span className="App-header-name">{this.props.name}</span>;
    }
}

class TextBlock extends React.Component<{ text: string }> {
    render() {
        return <p className="text-block">{this.props.text}</p>;
    }
}

class Contacts extends React.Component {
    render() {
        return (
            <div className="contacts">
                <p>Contacts:</p>
                <p><a href="mailto:contact@jack-kovzel.com">contact@jack-kovzel.com</a></p>
                <p><a href="https://www.linkedin.com/in/jack-kovzel/">
                    <img className="contact-social"
                         src="https://cdn-icons-png.flaticon.com/512/174/174857.png"></img>
                </a>
                </p>
            </div>
        );
    }
}

class HelloTextBlock extends React.Component<{ text: string }> {
    render() {
        return <p className="hello-text-block text-block">{this.props.text}</p>;
    }
}

class App extends React.Component<{}> {
    readonly onResize: () => void;

    constructor() {
        super({});

        this.onResize = () => {
            const container = document.getElementById('container');

            if(container) {
                container.style.height = '100%'
            }
        }
    }

    componentDidMount() {
        window.addEventListener('resize', () => this.onResize())
    }

    componentDidCatch(error: Error, errorInfo: React.ErrorInfo) {
        window.removeEventListener('resize', this.onResize)
    }

    render() {
        return (
            <div className="App">
                <header className="App-main-text" id="container">
                    <Name name="Evgeny Kovzel"/>
                    <HelloTextBlock text="Hello, my name is Evgeny."/>
                    <TextBlock text="I have more than 6 years in Web Development. Especially in Back-end Development."/>
                    <TextBlock
                        text="Strong knowledge of back-end technologies such as PHP (Symfony 5/Flex) in conjunction with technologies and instruments such as Elasticsearch, Git, MySQL, RabbitMQ, Docker, Behat, PHPUnit, Jest."/>
                    <TextBlock
                        text="Also have experience with front-end development and technologies such as jQuery, Node.js, ReactJS."/>
                    <TextBlock
                        text="Also have experience setting up and working with AWS resources such as CloudWatch, Lambda, SQS, ECR, ECS, EC2, etc."/>
                    <Contacts/>
                    <Particles
                        id="tsparticles"
                        options={Config}
                    />
                </header>
            </div>
        );
    }
}

export default App;
